import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import Header from '../../components/ui/Header';
import HeroSection from './components/HeroSection';
import Footer from './components/Footer';

const Inscription = () => {
   
  return (
    <>
      <Helmet>
        <title>Htel Luxe Haven - L o le luxe rencontre lauthenticit</title>
        <meta 
          name="description" 
          content="Dcouvrez un luxe raffin  lHtel Luxe Haven. Profitez dune hospitalit unique avec des expriences saisonnires, des services de classe mondiale et un accueil personnalis au cur de New York." 
        />
        <meta 
          name="keywords" 
          content="htel de luxe, htel boutique, htel New York, hbergement premium, voyage de luxe, expriences htelires" 
        />
        <meta property="og:title" content="Htel Luxe Haven - L o le luxe rencontre lauthenticit" />
        <meta property="og:description" content="Dcouvrez le luxe raffin  lHtel Luxe Haven avec des expriences saisonnires et des services haut de gamme." />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://luxehaven.com" />
        <link rel="canonical" href="https://luxehaven.com" />
      </Helmet>

      <div className="min-h-screen bg-background">
        {/* Navigation Header */}
        <Header />

        {/* Contenu principal */}
        <main>
        {/* Section Hro avec exprience immersive */}
          <HeroSection />
         
        </main>

        {/* Pied de page */}
        <Footer />

      </div>
    </>
  );
};

export default Inscription;
